import uniIcon from "@/components/uni-icons/uni-icons.vue";
export default {
	components:{
		uniIcon
	},
	data() {
		return {
			phone: '',
			password: '',
			passType: 'password',
			isPhoneClear: false,
			isPassClear: false,
			checkProtocol: true
		};
		
		},
	methods:{
		backto:function(){
			uni.navigateBack({
				delta: 1
			});
		},
		showPass: function(){
			this.passType = this.passType==='password'?'text':'password'
		},
		clearPhone: function(){
			this.phone = ''
		},
		clearPass: function(){
			this.password =''
		},
		showCloseIcon:function(e){
			if (e.target.value){
				this.isPhoneClear = true
			}else {
				this.isPhoneClear = false
			}
		},
		showPassClearIcon: function(e){
			if(e.target.value) {
				this.isPassClear = true
			}else {
				this.isPassClear = false
			}
			
		},
		agreeProtocol: function(){
			this.checkProtocol = !this.checkProtocol
			
		},
		getValidCode:function(){
			uni.request({
				url: this.apiServer + '/ul/verification',
				method: 'POST',
				data: {
					phone:　this.phone
				},
				success: res => {
					uni.showToast({
						title: '验证码已发送',
						duration: 2000,
						icon: 'none'
					});
				},
				fail: () => {},
				complete: () => {}
			});
		},
		formSubmit: function(e){
			let data = e.detail.value;
			let code = data.code;
			delete data.code;
			console.log(data);
			
			if(!data.phone){
				uni.showToast({
					title: '请输入电话号码！',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if(!data.password){
				uni.showToast({
					title: '请输入密码',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			// if(data.contPhone.length){
			// 	uni.showToast({
			// 		title: '电话号码需要11位',
			// 		icon: 'none',
			// 		duration: 2000
			// 	});
			// 	return ;
			// }
			// if(data.contPass < 6){
			// 	uni.showToast({
			// 		title: '密码最少6位',
			// 		icon: 'none',
			// 		duration: 2000
			// 	});
			// 	return ;
			// }
			if(!code){
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			if (!this.checkProtocol){
				uni.showToast({
					title: '请同意轻纺车网协议服务',
					icon: 'none',
					duration: 2000
				});
				return ;
			}
			uni.request({
				url: this.apiServer+'/ul/registration',
				method: 'POST',
				header: {
					'content-type': 'application/json'
				},
				data: { 
					phone: data.phone,
					password: data.password,
					verification: code
				},
				success: res => {
					console.log(res.data);
					if (res.data.status  == 0){
						uni.switchTab({
							url: "/pages/qing-f-c/index"
						})
					}
				},
				fail: (err) => {
					console.log(err.data)
				},
				complete: () => {}
			});
			
		}
	
	}
}
